<template>
  <div class="container">
    <div class="Portfolio">
      <div class="inner">
        <h3 class="title">{{ $t('my_works') }}</h3>
        <div class="itemsWrapper">
          <div
            class="item" 
            v-for="post in arrPosts" 
            :key="post.index"
          >
            <div class="container">
              <div class="inner">
                <img :src="post.img" alt="img" class="image" />
                <div class="info">
                  <h3 class="subtitle">{{ post.title }}</h3>
                  <div class="badgeList">
                    <span class="badge">{{ post.tags.year }}</span>
                    <span
                        v-for="technology in post.tags.technology"
                        :key="technology" class="badge"
                    >
                      {{ technology }}
                    </span>
                  </div>
                  <p v-if="$i18n.locale === 'en'" class="text">{{ post.text_en }}</p>
                  <p v-else class="text">{{ post.text_ru }}</p>
                  <div>
                    <a
                      :href="post.url"
                      target="_blank"
                      rel="noreferrer"
                      class="button"
                    >
                      {{ $t('go_to') }}
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <button
          @click="showMore"
          class="loadMoreBtn"
          v-if="arrPosts.length < this.$store.state.posts.length"
        >
          {{ $t('load_more') }}
        </button>
        <PreLoader v-show="onBottom" />
      </div>
    </div>
    <up-btn v-show="scroll" />
  </div>
</template>

<script>
import upBtn from '@/components/upBtn';
import PreLoader from "@/components/preLoader";

export default {
  components: {PreLoader, upBtn },
  data() {
    return {
      visiblePosts: 4,
      step: 4,
      scroll: false,
      onBottom: false
    };
  },
  mounted() {
    window.addEventListener('scroll', this.showUpBtn);
    this.showMoreOnScroll()
  },
  beforeUnmount() {
    window.removeEventListener('scroll', this.showUpBtn);
  },
  computed: {
    arrPosts() {
      let postsList = this.$store.state.posts;
      let reversList = postsList.slice().reverse();
      return reversList.slice(0, this.visiblePosts);
    },
  },
  methods: {
    showMore() {
      this.visiblePosts = this.step + this.arrPosts.length;
    },
    showUpBtn() {
      if(window.scrollY > 200) {
        this.scroll = true
      } else {
        this.scroll = false
      }
    },
    showMoreOnScroll() {
      window.onscroll = () => {
        let bottomOfWindow = Math.max(window.pageYOffset, document.documentElement.scrollTop, document.body.scrollTop) + window.innerHeight === document.documentElement.offsetHeight
        if (bottomOfWindow && window.innerWidth >= 1281 && this.arrPosts.length < this.$store.state.posts.length) {
          this.onBottom = true
          setTimeout(this.showMore, 1500)
        } else {
          this.onBottom = false
        }
      }
    }
  },
};
</script>

<style lang="scss">
.list-enter-active,
.list-leave-active {
  transition: all 0.5s ease;
}

.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateX(30px);
}

.Portfolio {
  .inner {
    position: relative;
    padding-bottom: 60px;

    @media (max-width: 768px) {
      padding-bottom: 35px;
    }

    .title {
      text-align: center;
      font-size: 32px;
    }

    .itemsWrapper {
      display: flex;
      flex-direction: column;
    }

    .item {
      padding-top: 75px;

      @media (max-width: 768px) {
        padding-top: 35px;

        &:last-child {
          padding-bottom: 35px;
        }
      }

      &:not(:last-child) {
        border-bottom: 1px solid $lightGray;
      }

      .inner {
        display: flex;
        align-items: center;

        @media (max-width: 1024px) {
          flex-direction: column;
        }

        .image {
          cursor: pointer;
          transition: 0.3s;
          max-width: 100%;
          height: auto;

          &:hover {
            transform: scale(1.1);
            transition: 0.3s;
          }
        }

        .info {
          margin-left: 115px;

          @media (max-width: 1024px) {
            margin-left: 0;
            text-align: center;
          }

          .subtitle {
            font-size: 24px;
            line-height: 30px;
            font-weight: 800;
            letter-spacing: 0.15em;
            margin-bottom: 8px;

            @media (max-width: 1024px) {
              margin-top: 40px;
              letter-spacing: 0;
              margin-bottom: 5px;
            }
          }

          .badgeList {
            .badge {
              display: inline-block;
              background: $white;
              border: 1px solid $blue;
              padding: 2px 10px;
              font-size: 12px;
              font-weight: 700;
              line-height: 1;
              border-radius: 50px;
              text-align: center;
              transition: 0.5s;
              cursor: default;
              color: $blue;
              white-space: nowrap;
              vertical-align: baseline;

              &:not(:last-child) {
                margin-right: 5px;
              }

              &:hover {
                background: $blue;
                color: $white;
                transition: 0.5s;
              }
            }
          }

          .text {
            max-width: 460px;
            width: 100%;
            color: $gray;
            margin: 15px 0 25px;

            @media (max-width: 1024px) {
              max-width: unset;
            }
          }

          .button {
            display: inline-block;
            padding: 10px 25px;
            background: $white;
            font-size: 14px;
            border-radius: 12px;
            min-width: 180px;
            text-align: center;
            border: 1px solid $blue;
            color: $blue;
            transition: 0.2s ease-in-out;
            text-decoration: none;

            &:hover {
              background: $blue;
              color: $white;
              text-decoration: none;
            }
          }
        }
      }
    }

    .loadMoreBtn {
      display: none;
      margin: 0 auto;
      padding: 10px 25px;
      background: $blue;
      font-size: 14px;
      border-radius: 12px;
      min-width: 180px;
      text-align: center;
      border: 1px solid $white;
      color: $white;
      transition: 0.2s ease-in-out;
      text-decoration: none;
      cursor: pointer;
      outline: none;
      position: absolute;
      bottom: 30px;
      left: 50%;
      transform: translateX(-50%);

      @media (max-width: 1280px) {
        display: block;
      }

      &:hover {
        background: $nila-blue;
      }
    }
  }
}
</style>